<template>
  <section class="form">
    <form @submit.prevent="submitForm">
      <input type="text" placeholder="First Name" v-model="formData.fname" />
      <input type="text" placeholder="Last Name" v-model="formData.lname" />
      <input type="email" placeholder="Email" required v-model="formData.email" />
      <input type="text" placeholder="Website Adress" v-model="formData.website" />
      <input type="text" placeholder="Linkedin Profile" v-model="formData.linkedin" />
      <label>
        How many years of experience do you have with Facebook Marketing?
        <select v-model="formData.experience">
          <option value="no experience">No Experience</option>
          <option value="0-1 Years">0-1 Years</option>
          <option value="1-2 Years">1-2 Years</option>
          <option value="2 or more years">2 or more years</option>
        </select>
      </label>
      <label>
        What was the biggest campaign budget you have managed in a single month?
        <input type="range" min="1000" max="500000" v-model.number="formData.maxBudget" />
        {{ formData.maxBudget }}
      </label>
      <button>Submit</button>
    </form>
      <button @click="resetForm">Reset Form</button>
  </section>
</template>

<script>
import { marketService } from '@/services/marketers.service';

export default {
  name: 'Home',
  data() {
    return {
      formData: {
        fname: '',
        lname: '',
        email: '',
        website: '',
        linkedin: '',
        experience: 'no experience',
        maxBudget: 1000
      }
    }
  },
  methods: {
    submitForm() {
      marketService.save(JSON.parse(JSON.stringify(this.formData)))
    },
    resetForm() {
      this.formData = {
        fname: '',
        lname: '',
        email: '',
        website: '',
        linkedin: '',
        experience: '',
        maxBudget: 1000
      }
    }
  }
}
</script>

<style lang="scss">
  .form {
    text-align: center;

    &>button {
      background-color: red;
    }
  }
  form {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 4rem;

    &>*:not(:last-child) {
      margin-bottom: 2rem;
    }
    background-color: initial;
  }
</style>